.hero-img{
    width:100%;
    background: rgba(0, 0,0,0.5);
    height: 70vh;
    position: relative;
}
.hero-img::before{
    content:"";
    background: url("https://images.unsplash.com/photo-1555421689-491a97ff2040?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=870&q=80");
    background-size: cover;
    background-position: center;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;

}


.heading{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    padding-top: 25%;
    justify-content: center;
    align-items: center;
}
.hero-img{
    font-size: 2.4rem;
}
.hero-img{
    font-size:1.4rem;
    text-align: center;
    padding-bottom: 20rem;
    align-items: center;
}
@media screen and (max-width:640px) {
    .hero-img{
        font-size: 1rem;
        padding-bottom: 5rem;
    }
    
}
