label{
    color: #fff;
}
form{
    display:flex;
    flex-direction:column;
    padding:4rem 1rem ;
    margin: auto;
    max-width: 600px;
}
form input,form textarea{
    margin-bottom: 1rem;
    padding: 1rem;
    font-size: 1.2rem;
    background-color: rgba(255, 255, 255, 0.1);
    border-color: rgba(255, 255,255,0.1);
    color:#f4f4f4;
}
@media  screen  and (max-width:1440px){
    form input,form textarea{
        margin-bottom: 0.5rem;
        padding: 0.5rem ;
        font-size: 0.5rem;
        background-color: rgba(255, 255, 255, 0.1);
        border-color: rgba(255, 255,255,0.1);
        color:#f4f4f4;
    }
    
}